.generalDiv {
    position: relative;
    padding-top: 6rem;
    padding-left: 6rem;
    padding-right: 6rem;
    padding-bottom: 4rem;
}

.portfolioText h2 {
    font-size: 4.2rem;
}

.portfolioText p {
    font-size: 1.6rem;
    width: 50%;
    text-align: left;
    padding-top: 1rem;
}

.portfolioImages {
    columns: 3;
    position: relative;
    column-gap: 2rem;
    padding-top: 4rem;
}

.imageDescription {
    font-size: 2.4rem;
}

.mappedData p {
    padding-bottom: 3rem;
}


@media screen and (max-width:768px) {
    .generalDiv {
        padding-left: 3rem;
        padding-right: 3rem;
    }
    .portfolioText p{
        width: 86%;
    }
    .imageDescription{
        font-size: 100%;
    }
}