*,
*::before,
*::after {
    margin: 0;
    border-radius: inherit;
    padding: 0;
}
.mainDiv {
    background-color: #000;
    padding: 6rem;
    opacity: 80%;
    height: 25rem;
}

.mainDiv h2 {
    color: #fff;
    font-size: 4rem;
} 

.mainDiv p {
    font-size: 1.6rem;
    color: #fff;
    width: 50%;
    padding-top: 2rem;
    margin-bottom: 5rem;
}

.mainDiv a {
    color: #000;
    font-weight: 600;
    font-size: 1.8rem;
    padding: 1.6rem 2rem;
    background-color: #fff;
}

@media screen and (max-width:768px) {
    .mainDiv{
        padding: 3rem;
    }
    .mainDiv h2{
        font-size: 2.2rem;
    }
    .mainDiv p{
        font-size: 1.4rem;
        width: 90%;
    }
}