a {
    color: #000;
}

.nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-left: 6rem;
    padding-right: 6rem;
    align-items: center;
    padding-top: 3rem;
}

.logo {
    font-size: 2.8rem;
}

.nav li {
    display: inline-block;
    padding-left: 2rem;
    font-weight: 200;
    font-size: 1.6rem;
}

ul a {
    color: rgba(0, 0, 0, 0.8);
}

.active {
    border-bottom: 0.19rem solid #000;
    padding-bottom: 0.3rem;
}

nav{
    /* border-bottom: 0.3rem solid black; */
    height: auto;
}

.checkbox {
    position: absolute;
    display: block;
    height: 0.32rem;
    width: 0.48rem;
    top: 0.7rem;
    left: 0.7rem;
    z-index: 5;
    opacity: 0;
    cursor: pointer;
}

.hamburgerLines {
    margin-left: 0.7rem;
    padding-top: 0.7rem;
    display: block;
    animation: fadeIn 0.2s;
    width: 20%;
}

.fa{
    font-size: 3rem;
}

.hamburgerLine {
    width: 4rem;
    margin: 0.1rem;
    height: 0.6rem;
    display: block;
}

.hamburgerLine:nth-child(1) {
    margin: 0;
}

.xlines {
    margin-left: 0.7rem;
    display: block;
    animation: fadeIn 0.2s;
}

.xline {
    background: #000;
    width: 4rem;  position: relative;
    top: 62px;
    height: 0.6rem;
    display: block;
    /* margin-top: 20%; */
  
}

.xline:nth-child(1) {
    transform: rotate(45deg);
}

.xline:nth-child(2) {
    transform: rotate(-45deg) translateY(-0.4rem) translateX(0.4rem);
}

.hamburgerMenu li {
    animation: fadeIn 0.2s;
    list-style: none;
    /* padding: 0 30rem 1rem 1rem; */
    background-color: red;
    /* overflow: hidden; */
    position: relative;
    top: 62px;
    /* float: left; */
}

.hamburgerMenu li:nth-child(1) {
    padding-top: 3rem;
}

input [type="checkbox"]:checked ~ .hamburgerLines {
    display: none;
}

input [type='checkbox']:not(:checked) ~ .hamburgerMenu {
    display: none;
}
input [type='checkbox']:not(:checked) ~ .hamburgerMenu {
    display: none;
}

@media screen and (max-width:768px) {
    .nav{
        padding-left: 3rem;
        align-items: center;
        padding-right: 3rem;
        position: relative;
    }
    .desktop{
        display: none;
    }
    .hamburgerLines{
        margin-left: 66%;
        align-items: center;
        padding-bottom: 0.5rem;
    }
}