.hero {
    font-size: 4rem;
    width: 60%;
    padding-bottom: 4rem;
    padding-top: 6rem;
    padding-left: 6rem;
    position: relative;
    padding-right: 6rem;
}

.heroText h1 {
    font-weight: 500;
    font-size: 5.6rem;
}

@media screen and (max-width:768px) {
    .hero{
        padding-left: 3rem;
        padding-right: 3rem;
    }
    .heroText h1{
        font-size: 3rem;
        width: 40rem;
    }
}