.Footer {
    padding-left: 6rem;
    padding-right: 6rem;
    height: 20rem;
    padding-top: 4rem;
}

.Footer h2 {
    font-size: 2.8rem;
}

.footerLinks {
    display: flex;
    padding-top: 2.5rem;
    line-height: 2.5;
}

.footerLinks p {
    font-size: 1.6rem
}

.firstLink {
    width: 20%;
}

.secondLink {
    padding-left: 6rem;
}

@media screen and (max-width:768px) {
    .Footer{
        padding-left: 3rem;
        padding-right: 3rem;
        padding-top: 2rem;
    }
    .footerLinks p{
        font-size: 1.2rem;
    }
    .firstLink {
        width: 100%;
    }
}